body {
  background-image: url("http://subtlepatterns.com/patterns/handmadepaper.png");
  margin-top: 40px;

  .row-space {
    margin-bottom: 40px;
  }

  .page-header {
    border-bottom: 1px dotted #c0c0c0;

    .title {
      padding: 0;
      margin: 0;
    }
  }

  .tab-pane {
    .title {
      border-bottom: 1px dotted #e0e0e0;
      padding-bottom: 5px;
      margin-bottom: 25px;

      span {
        margin-top: 10px;
        font-size: 20px;
        display: block;
      }
    }
  }

  .form-actions {
    padding-top: 20px;
  }
}

.btn-glyphicon {
  padding: 8px;
  background: #ffffff;
  margin-right: 4px;
}

.icon-btn {
  padding: 1px 15px 3px 2px;
  border-radius: 50px;
}

.panel.with-nav-tabs .panel-heading {
  padding: 5px 5px 0 5px;
}

.panel.with-nav-tabs .nav-tabs {
  border-bottom: none;
}

.panel.with-nav-tabs .nav-justified {
  margin-bottom: -1px;
}

